html {
  box-sizing: border-box;
  font-size: 16px;
}

*, *:after, *:before {
  box-sizing: inherit;
}

body {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  background-color: #ECF7FB;
  font-weight: 400;
}

img,
video,
audio {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
  color: #000;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
}

h1, h2 {
  font-family: "Anton", sans-serif;
}

h3, h4 {
  font-family: "Roboto", sans-serif;
}

h1 {
  font-size: clamp(2.44rem, 2.38vw + 1.85rem, 3.75rem);
}

h2 {
  font-size: clamp(1.56rem, 1vw + 1.31rem, 2.11rem);
}

ul {
  padding-inline: 1.25rem;
}

.header {
  background-color: #062446;
  text-align: center;
  padding-block-start: 3.125rem;
  padding-block-end: 6.25rem;
  color: #ECF7FB;
}
.header__container {
  width: 80%;
  max-width: 1024px;
  margin-inline: auto;
}
.header__saludo {
  margin: 0 auto;
  font-size: clamp(1.56rem, 1vw + 1.31rem, 2.11rem);
  width: 10ch;
  white-space: nowrap;
  overflow: hidden;
  animation: escribir 3s steps(10), cursor 0.3s infinite;
}
.header__h1 {
  margin-block: 1.25rem;
  color: #062446;
  animation: mostrar 3s 4s forwards;
}
.header__dev {
  margin: 0;
  font-size: clamp(1.56rem, 1vw + 1.31rem, 2.11rem);
  color: #062446;
  animation: mostrar 3s 6s forwards;
}

.borde {
  border-inline-end: 3px solid #000;
}

@keyframes escribir {
  0% {
    width: 0;
  }
  100% {
    width: 10ch;
  }
}
@keyframes cursor {
  50% {
    border-color: transparent;
  }
}
@keyframes mostrar {
  0% {
    color: #062446;
  }
  100% {
    color: #ECF7FB;
  }
}
.aboutme__container {
  margin-block-start: -3.125rem;
  background-color: #FFFFFF;
  width: 80%;
  max-width: 1024px;
  margin-inline: auto;
  padding-block-start: 3.125rem;
  display: flex;
  flex-direction: column;
  gap: 3.125rem;
  align-items: center;
  position: relative;
  z-index: 1;
}
.aboutme__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .aboutme__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}
.aboutme__contents {
  padding: 3.125rem 1.25rem 0;
}
@media (min-width: 768px) {
  .aboutme__contents {
    padding: 0 1.25rem;
  }
}
.aboutme__h2 {
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.625rem;
}
.aboutme__h2::before {
  content: "";
  display: block;
  width: 5px;
  height: 40px;
  background-color: #04445C;
}
.aboutme__text {
  margin-block-start: 1.25rem;
}
.aboutme__strong {
  font-weight: 400;
}
.aboutme__contact {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1.25rem;
}
.aboutme__comencemos {
  font-weight: 700;
  text-transform: uppercase;
  padding-inline: 1.25rem;
  text-align: center;
}
.aboutme__email {
  display: flex;
  align-items: center;
  color: #FFFFFF;
  gap: 0.3125rem;
  background-color: #04445C;
  border-radius: 5px;
  padding: 0.3125rem;
}
.aboutme__email:hover {
  background-color: #062446;
}

.frame {
  width: 50%;
}
.frame__img {
  height: 260px;
  object-fit: cover;
  transform: rotate(-6deg);
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
  border: 10px solid #fff;
}

.main {
  background-color: #062446;
  margin-top: -3.125rem;
  padding-top: 6.25rem;
  position: relative;
  z-index: 0;
  color: #FFFFFF;
  padding-block-end: 3.125rem;
}
.main__container {
  width: 80%;
  max-width: 1024px;
  margin-inline: auto;
  padding-block-end: 3.125rem;
}
.main__h2 {
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.625rem;
}
.main__h2::before {
  content: "";
  display: block;
  width: 5px;
  height: 40px;
  background-color: #04445C;
}
.main__invitation {
  margin: 0;
}

.jobs {
  margin-block-start: 3.125rem;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (min-width: 768px) {
  .jobs {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 2;
    background-image: url(../img/linea.png);
    background-repeat-x: no-repeat;
    background-position: center;
  }
}
.jobs__apps {
  grid-column: 1/2;
  grid-row: 1/2;
  background-color: #ECF7FB;
}
.jobs__static {
  grid-column: 2/3;
  grid-row: 2/3;
  background-color: #ECF7FB;
}
.jobs__heading {
  display: flex;
  gap: 0.3125rem;
  background-color: #04445C;
  align-items: center;
  padding-inline: 0.625rem;
  height: 59px;
}
.jobs__heading:hover {
  cursor: pointer;
}
.jobs__h3 {
  font-weight: 700;
}
.jobs__list {
  margin: 1.25rem auto;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.25rem;
  justify-content: center;
  color: #000;
}

.ocultar {
  height: 59px;
  overflow: hidden;
}

.job {
  background-color: #FFFFFF;
}
.job__link {
  position: relative;
}
.job__link:hover::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  left: 0;
  top: 0;
}
.job__link:hover::before {
  content: "Visitar";
  color: #FFFFFF;
  z-index: 1;
  width: 20px;
  height: 20px;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.job__img {
  border: 1px solid #062446;
}
.job__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.625rem;
  justify-content: space-between;
  padding-inline: 0.625rem;
}
@media (min-width: 1024px) {
  .job__content {
    flex-direction: row;
    padding-block: 1.25rem;
  }
}
.job__h4 {
  text-transform: uppercase;
  margin-block-end: 0;
}
@media (min-width: 1024px) {
  .job__h4 {
    margin: 0;
  }
}
.job__details {
  display: flex;
  align-items: flex-start;
  gap: 0.625rem;
}
@media (min-width: 1024px) {
  .job__details {
    align-items: center;
  }
}
.job__type {
  font-size: clamp(1rem, 0.34vw + 0.91rem, 1.19rem);
  border-radius: 5px;
  color: #FFFFFF;
  padding: 0 0.3125rem;
  margin-block-start: 0;
}
@media (min-width: 1024px) {
  .job__type {
    margin: 0;
  }
}
.job__type--app {
  background-color: #04445C;
}
.job__type--prototipo {
  background-color: #D41717;
}

.skills__container {
  width: 80%;
  max-width: 1024px;
  margin-inline: auto;
  background-color: #FFFFFF;
  margin-block-start: -3.125rem;
  position: relative;
  z-index: 0;
  padding-block-start: 6.25rem;
  padding-block-end: 3.125rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .skills__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }
}
.skills__h2 {
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.625rem;
}
.skills__h2::before {
  content: "";
  display: block;
  width: 5px;
  height: 40px;
  background-color: #04445C;
}
.skills__contents {
  width: 100%;
  padding: 3.125rem 1.25rem 0;
}
@media (min-width: 768px) {
  .skills__contents {
    padding: 0 1.25rem;
  }
}
.skills__skill {
  color: #062446;
}
.skills__skill span {
  color: #000;
}

.footer {
  background-color: #062446;
  padding-block-end: 3.125rem;
}
.footer__container {
  width: 80%;
  max-width: 1024px;
  margin-inline: auto;
  background-color: #FFFFFF;
  text-align: center;
  padding-block: 1.25rem;
}
.footer__text {
  margin-block-start: 0;
  font-weight: 700;
}
.footer__nav {
  margin-inline: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.25rem;
  list-style-type: style color;
}
.footer__link img {
  height: 1.875rem;
  width: 1.875rem;
}